(function () {
	'use strict';
	angular.module('homeCtrl', ['benefitsDirective', 'productService', 'productCtrl', 'newsDirective', 'configService'])
		.controller('HomeCtrl', [
			'$scope',
			'$log',
			'ProductService',
			'ConfigService',
			homeCtrl
		]);

	function homeCtrl($scope, $log, ProductService, ConfigService) {
		$log.log('home ctrl');

		$scope.init = function() {
			$scope.$parent.constructorHeader = false;
			$scope.showLoadMoreBtn = true;
			$scope.catalogItems = [];
			$scope.featuredProduct = {};
			$scope.featuredArticle = ConfigService.homeArticleSlug;

			$scope.getFeaturedProduct();
			$scope.getProductList();
		};

		$scope.getProductList = function () {
			ProductService.getHomeList({skip: $scope.catalogItems.length, count: 8})
				.then(function (data) {
					// Success
					if (data.length === 0){
						$scope.showLoadMoreBtn = false;
					} else {
						Array.prototype.push.apply($scope.catalogItems, data);
					}
				}, function (err) {
					// Error
					$log.log(err);
				});
		};

		$scope.getFeaturedProduct = function() {
			ProductService.getFeatured()
				.then(function(data) {
					$scope.featuredProduct = data;
				}, function(err) {
					$log.log(err)
				});
		};

		$scope.init();
	}

})();
